import { Component, ContentChild, Directive, Input, TemplateRef } from '@angular/core';
import { DefaultPaymentCardNumberFormat, PaymentCardNumberFormat } from '@backbase/ui-ang/payment-card-number-pipe';

@Directive({ selector: 'ng-template[bbCustomProductItemTermDepositTitle]' })
export class CustomProductItemTermDepositTitleDirective {
  constructor(public templateRef: TemplateRef<any>) {}
}

@Directive({ selector: 'ng-template[bbCustomProductItemTermDepositDetailsFormatter]' })
export class CustomProductItemTermDepositDetailsFormatterDirective {
  constructor(public templateRef: TemplateRef<any>) {}
}

@Directive({ selector: 'ng-template[bbCustomProductItemTermDepositMainSection]' })
export class CustomProductItemTermDepositMainSectionDirective {
  constructor(public templateRef: TemplateRef<any>) {}
}

@Component({
  selector: 'bb-product-item-term-deposit-ui',
  templateUrl: './product-item-term-deposit.component.html',
})
export class ProductItemTermDepositComponent {
  @ContentChild(CustomProductItemTermDepositTitleDirective, { static: true })
  customProductItemTermDepositTitle: CustomProductItemTermDepositTitleDirective | undefined;

  @ContentChild(CustomProductItemTermDepositDetailsFormatterDirective, { static: true })
  customProductItemTermDepositDetailsFormatter: CustomProductItemTermDepositDetailsFormatterDirective | undefined;

  @ContentChild(CustomProductItemTermDepositMainSectionDirective, { static: true })
  customProductItemTermDepositMainSection: CustomProductItemTermDepositMainSectionDirective | undefined;

  readonly hostRef: ProductItemTermDepositComponent = this;
  /**
   * The card Title value. Default is empty string
   */
  @Input() title: string | undefined;
  /**
   * The product Number. Default is empty string
   */
  @Input() productNumber: string | undefined;
  /**
   * Configuration of how the product number should be formatted.
   */
  @Input() productNumberFormat: PaymentCardNumberFormat = DefaultPaymentCardNumberFormat;
  /**
   * The card currency value. Default is empty string
   */
  @Input() currency?: string | undefined;
  /**
   * The card Amount value
   */
  @Input() amount?: number | undefined;
  /**
   * The card additional main Section currency value
   */
  @Input() mainSectionCurrency?: string | undefined;
  /**
   * The card additional main Section Amount value
   */
  @Input() mainSectionAmount?: number | undefined;
  /**
   * The card additional main Section Amount value
   */
  @Input() mainSectionTitle?: string | undefined;
  /**
   * The card highlight the amount and currency value
   */
  @Input() highlight?: boolean;
  /**
   * The product details info values
   */
  @Input()
  productDetailsInfo?: {
    [key: string]: any;
  };
  /**
   * The active state of card (it adds active class for blue line)
   */
  @Input() active = true;
  /**
   * The favorite state of card (add favorite icon next to title)
   */
  @Input() favorite = false;
  /**
   * The favorite icon color (define star icon color)
   */
  @Input() favoriteIconColor = 'primary';
}
